import React, { Component } from "react";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "../../App.css";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import axios from "axios";
import { Product } from "../../models/ProductObject";
import $ from "jquery";

class ProductDetailsModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            product: null,
            infoMessage: null,
        };
    }

    componentWillReceiveProps(props) {
        console.log(props);
        this.setState({ loading: true });
        this.fetchProduct(props.offerId);
    }

    fetchProduct = (offerId) => {
        if (offerId == -1) {
            return;
        }

        let apiUrl = "https://yr2yl0lpui.execute-api.us-east-1.amazonaws.com/dev";
        let self = this;
        axios
            .get(apiUrl + "/offers/" + offerId, {
                headers: {
                    "x-api-key": "A8x8nBruJt5Obic2pzNdF3AtEzL4QOea6hN13D8A",
                },
            })
            .then(function (response) {
                console.log(response.data);
                if (response.data.status == "Inactive") {
                    self.setState({
                        product: null,
                        loading: false,
                    });
                } else {
                    self.setState({
                        product: new Product(response.data),
                        loading: false,
                    });
                }
            })
            .catch(function (error) {
                console.log(error);
            });
    };

    showMessage(message, type = "success") {
        let self = this;
        this.setState({
            infoMessage: {
                message: message,
                type: type,
            },
        });

        // setTimeout(function () {
        //     self.setState({
        //         infoMessage: null,
        //     });
        // }, 5000);
    }

    getInfoMessage() {
        let message = this.state.infoMessage.message;
        let colourStyle = this.state.infoMessage.type;
        let showLoading = this.state.infoMessage.loading || false;

        return <div className={"col-8 text-" + colourStyle}>{message}</div>;
    }

    validate() {
        let min = $("#minPrice").val();
        let max = $("#preferredPrice").val();

        if (parseInt(min) < 1) {
            this.showMessage("Your minimum price is less than R1. You're not giving it away for free are you?", "danger");
            return false;
        } else if (parseInt(min) > parseInt(max)) {
            this.showMessage("Your minimum price can't be more than your preferred price", "danger");
            return false;
        }

        if (parseInt(max) < 1) {
            this.showMessage("Your preferred price is less than R1. You're not giving it away for free are you?", "danger");
            return false;
        } else if (parseInt(min) > parseInt(max)) {
            this.showMessage("Your preferred price can not be less than your minimum price", "danger");
            return false;
        }

        return true;
    }

    updatePrice() {
        if (!this.validate()) {
            return;
        }

        this.showMessage("Saving...");

        let self = this;
        let min = $("#minPrice").val();
        let max = $("#preferredPrice").val();

        let apiUrl = "https://yr2yl0lpui.execute-api.us-east-1.amazonaws.com/dev";
        axios
            .post(
                apiUrl + "/offers/thresholds/update",
                {
                    offerId: this.state.product.offerId,
                    max: parseFloat(max),
                    min: parseFloat(min),
                    increment: parseInt(1),
                    productId: this.state.product.plid,
                },
                {
                    headers: {
                        "x-api-key": "A8x8nBruJt5Obic2pzNdF3AtEzL4QOea6hN13D8A",
                    },
                }
            )
            .then(function (response) {
                console.log(response);
                self.showMessage("Saved!");
            })
            .catch(function (error) {
                console.log(error);
                self.showMessage(error.message, "danger");
            });
    }

    detailsTabPanel() {
        return (
            <TabPanel style={{ padding: "10px" }}>
                <div className="row" style={{ marginBottom: "30px" }}>
                    <div className="col-12">
                        <h5>Offer Details:</h5>
                    </div>
                </div>
                <div className="row">
                    <div className="col-4">
                        <img
                            src={this.state.product.image}
                            className="img-fluid"
                            style={{
                                width: "400px",
                                marginBottom: "10px",
                                border: "1px solid #d2d1d1",
                            }}
                        />
                        <h6>
                            <b>Price: R {this.state.product.currentPrice}.00</b>
                        </h6>
                        <a className="btn btn-success" style={{ color: "#fff", marginTop: "10px" }} href={this.state.product.url} target="_blank">
                            View Offer on Takealot
                        </a>
                    </div>
                    <div className="col-8">
                        <h5>
                            <b>{this.state.product.title}</b>
                        </h5>
                        <br />
                        <small>
                            <b>Brand: {this.state.product.brand}</b>
                        </small>
                        <br />
                        <br />
                        <h6>
                            <b>Description:</b>
                        </h6>
                        <br />
                        <div className="post__content" dangerouslySetInnerHTML={{ __html: this.state.product.description }}></div>
                    </div>
                </div>
            </TabPanel>
        );
    }

    settingTabPanel() {
        return (
            <TabPanel style={{ padding: "10px" }}>
                <div className="row" style={{ marginBottom: "30px" }}>
                    <div className="col-12">
                        <h5>Offer Settings:</h5>
                    </div>
                </div>
                <div className="row">
                    <div className="col-8">
                        <div className="row justify-content-center">
                            <div className="col-8">
                                <label className="form-label">Minimum price</label>
                                <input
                                    id="minPrice"
                                    type="number"
                                    className="form-control"
                                    defaultValue={this.state.product.minPrice}
                                    // onChange={(event) => setMin(event.target.value)}
                                />
                                <small style={{ color: "#ff0000" }} id="minError"></small>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-8">
                                <label className="form-label">Preferred sale Price</label>
                                <input
                                    id="preferredPrice"
                                    type="number"
                                    className="form-control"
                                    defaultValue={this.state.product.preferredSalePrice}
                                    // onChange={(event) => setMax(event.target.value)}
                                />
                                <small style={{ color: "#ff0000" }} id="maxError"></small>
                            </div>
                        </div>
                        <div className="row justify-content-center">
                            <div className="col-8">
                                <a className="btn btn-success" style={{ color: "#fff", marginTop: "30px" }} onClick={() => this.updatePrice()}>
                                    Save
                                    {/* {updating ? <i className="fa fa-spinner fa-spin" style={{ marginLeft: "5px" }}></i> : null} */}
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </TabPanel>
        );
    }

    render() {
        return (
            <div>
                <div
                    class="modal fade"
                    id="productDetailsModal"
                    tabindex="-1"
                    role="dialog"
                    aria-labelledby="productDetailsModalLabel"
                    aria-hidden="true"
                >
                    <div class="modal-dialog modal-xl" role="document">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h5 class="modal-title" id="productDetailsModalLabel">
                                    Product details
                                </h5>
                                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div class="modal-body">
                                <div className="col-12 text-left">
                                    <div className="container py-4">
                                        <div className="row">
                                            <div className={this.state.loading ? "col-12 text-center" : "col-12"}>
                                                {this.state.loading ? (
                                                    <i className="fa fa-spinner fa-spin fa-3x" style={{ marginTop: "100px" }}></i>
                                                ) : this.state.product != null ? (
                                                    <Tabs>
                                                        <TabList>
                                                            <Tab>Details</Tab>
                                                            <Tab>Settings</Tab>
                                                        </TabList>
                                                        {this.detailsTabPanel()}
                                                        {this.settingTabPanel()}
                                                    </Tabs>
                                                ) : (
                                                    <h3>This offer is inactive.</h3>
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {this.state.infoMessage != null ? (
                                <div className="modal-footer" style={{ display: "block" }}>
                                    <div className="row justify-content-center">{this.getInfoMessage()}</div>
                                </div>
                            ) : null}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export default ProductDetailsModal;
