export async function refreshLocalOffers() {
    if (guid.latest == null) {
        return;
    }

    try {
        const response = await axios.get(apiUrlPy + "/offers?guid=" + guid.latest, {
            headers: { "x-api-key": awsKey },
        });

        const prodList = [];
        response.data.map((item, key) => {
            prodList.push(new Product(item));
        });

        // const dispatch = useDispatch();
        // dispatch(set(products));

        return prodList;
    } catch (error) {
        console.log(error);
        return null;
    }
}

export async function getGroupedOffers(buyboxStatus, status) {
    try {
        if (guid.latest == null) {
            return null;
        }

        const buyboxParam = buyboxStatus != null ? "&buyboxStatus=" + buyboxStatus : "";
        const statusParam = status != null ? "&tatus=" + status : "";

        const response = await axios.get(apiUrlJs + "/offers/buyboxstatus?guid=" + guid.latest + buyboxParam + statusParam, {
            headers: { "x-api-key": awsKey },
        });

        const prodList = [];
        response.data.map((item, key) => {
            prodList.push(new Product(item));
        });
        return prodList;
    } catch (error) {
        console.log(error);
        return null;
    }
}

export async function whoami(guid) {
    try {
        if (guid == null) {
            console.log("No GUID provided to 'whoami'");
            return null;
        }

        const response = await axios.get(apiUrlPy + "/whoami?guid=" + guid, {
            headers: { "x-api-key": awsKey },
        });

        return new User(response.data[0]);
    } catch (error) {
        console.log(error);
        return null;
    }
}
