import React, { useState, useEffect, Component } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { connect, useDispatch } from "react-redux";
import { MDBDataTable } from "mdbreact";
import { Product } from "../../models/ProductObject";
import ProductDetailsModal from "../modals/ProductDetailsModal";
import $ from "jquery";

let mapStateToProps = (state) => {
    return {
        guid: state.auth.user.guid,
        products: state.products.map((product) => {
            const newProduct = Object.assign([], product);

            newProduct.autoPricingEnabled = (
                <div class="custom-control custom-switch">
                    <input
                        type="checkbox"
                        class="custom-control-input"
                        name={product.offerId + "_autoPricingSwitch"}
                        id={product.offerId + "_autoPricingSwitch"}
                        disabled={product.status == "Active" ? false : true}
                        onChange={() => {
                            new Product(product).toggleAutoPricing();
                        }}
                    />
                    <label class="custom-control-label" for={product.offerId + "_autoPricingSwitch"}></label>
                </div>
            );

            newProduct.status = (
                <span style={product.status == "Active" ? (product.isWinningBuybox ? { color: "#009900" } : { color: "#ff0000" }) : {}}>
                    {product.status == "Active" ? (product.isWinningBuybox ? "Winning" : "Losing") : "Disabled"}
                </span>
            );

            newProduct.url = (
                <a href={product.url} target="_blank">
                    View Offer
                </a>
            );
            return newProduct;
        }),
    };
};

function mapDispatchToProps(dispatch) {
    return {
        // storePageNumber: (pageNumber) => dispatch(storePageNumber(pageNumber)),
        // setRegistered: (data) => dispatch(setRegistered(data)),
    };
}

class ProductsTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            selectedOfferId: -1,
        };
    }

    didSelectRow(product) {
        console.log("REDIRRRRRRR");
        console.log(product);
        this.setState({
            selectedOfferId: product.offerId,
        });
        $("#productDetailsModal").modal("show");
    }

    render() {
        return this.props.products == null ? (
            <h1>No dice</h1>
        ) : (
            <div>
                <ProductDetailsModal offerId={this.state.selectedOfferId} />
                <MDBDataTable
                    entries={20}
                    striped
                    bordered
                    hover
                    data={{
                        columns: [
                            { label: "Title", field: "title", sort: "asc", fixed: true },
                            { label: "Barcode", field: "barCode", sort: "asc" },
                            { label: "GTIN", field: "gtin", sort: "asc" },
                            { label: "Minimum price", field: "minPrice", sort: "asc" },
                            { label: "Preferred price", field: "preferredSalePrice", sort: "asc" },
                            { label: "Current price", field: "currentPrice", sort: "asc" },
                            { label: "Status", field: "status", sort: "asc" },
                            { label: "Auto pricing", field: "autoPricingEnabled" },
                            { label: "Takelot link", field: "url" },
                        ],

                        rows: this.props.products.map((product) => {
                            product.clickEvent = () => this.didSelectRow(product);
                            return product;
                        }),
                    }}
                    style={{ zIndex: 999999 }}
                />
            </div>
        );
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductsTable);
