import { useDispatch } from "react-redux";
import { refreshLocalOffers } from "../helpers/api";
import { set } from "../state/actions";

const currentUserSellerId = {
    get latest() {
        return JSON.parse(window.localStorage.getItem("price.beater")).auth.user.sellerId;
    },
};

// WIP
export class Product {
    constructor(json) {
        this.title = json.title || json.productTitle;
        this.currentPrice = json.currentPrice || json.productPrice;
        this.rrp = json.rrp;
        this.offerId = json.offerId;
        this.gtin = json.gtin;
        this.url = json.productTakealotURL || json.url;
        this.status = json.status;
        this.barCode = json.mpBarcode;
        this.minPrice = json.minPrice;
        this.preferredSalePrice = json.maxPrice;
        this.increment = json.increment;
        this.brand = json.brand;
        this.buybox = json.buyBox;
        this.plid = json.productLineId;
        this.currentSellerId = json.currentSellerId;

        this.autoPricingEnabled = false;

        this.isWinningBuybox = this.currentSellerId == currentUserSellerId.latest;

        // Takealot direct
        this.description = json.description;
        this.image = json.image ? json.image.replace("{size}", "full") : "";
        this.plid = json.productLineId;
    }

    toggleAutoPricing() {
        this.autoPricingEnabled = !this.autoPricingEnabled;
        console.log("Toggled");
    }
}
