// WIP
export class User {
    constructor(json) {
        this.id = json.id;
        this.guid = json.guid;
        this.firstName = json.firstName;
        this.lastName = json.lastName;
        this.sellerId = json.sellerId;
        this.email = json.email;
        this.apikey = json.apikey;
    }

    static mock() {
        return new User({
            id: 23,
            guid: "1d10f499-d4a9-4a7e-bc8d-c26b31f74291",
            firstName: "Byron",
            lastName: "Coetsee",
            sellerId: "9760",
            email: "byroncoetsee@gmail.com",
            apikey: "1234567890",
            createdAt: "2020-04-02T16:28:45",
            updatedAt: "2020-04-02T16:28:45",
            deletedAt: null,
        });
    }
}

