import React, { useState, useEffect } from "react";
import AppLayout from "../components/layouts/app";
import { setRegistered } from "../state/actions/index";
import { connect } from "react-redux";
import SyncModal from "../components/modals/SyncModal";
import { CardWinning, CardLosing, CardTotal } from "./cards/CounterCards";

let mapStateToProps = (state) => {
    return {
        registered: state.registered,
        showModal: state.showModal,
    };
};

function mapDispatchToProps(dispatch) {
    return {
        setRegistered: (data) => dispatch(setRegistered(data)),
    };
}

const Dashboard = (props) => {
    const [showModal, setShowModal] = useState(props.showModal);
    useEffect(() => {
        if (props.showModal == 1) {
            props.setRegistered({
                registered: props.registered,
                showModal: 0,
            });
        }
    }, []);

    const handleClose = () => {
        setShowModal(0);
    };

    return (
        <AppLayout>
            <SyncModal show={showModal} handleClose={handleClose}>
                <div className="row justify-content-center">
                    <div className="col-10">
                        <h2>Welcome!</h2>
                        <br></br>
                        <h3>Your information is being pulled from Takealot. This might take up to 30 minutes.</h3>
                        <br></br>
                    </div>
                </div>
            </SyncModal>
        </AppLayout>
    );
};

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
